import { Injectable } from '@angular/core';
import { ConstantsService } from 'src/app/services/constants.service';
import { AngularFireDatabase } from '@angular/fire/database';
import { UtilsService } from 'src/app/services/utils.service';
import { AlertController } from '@ionic/angular';

@Injectable({
  providedIn: 'root'
})
export class NewsManagementService {
  heightTable = "775";
  columnsTable = [
    { title: "id", field: "id", align: "left", width: 50 },
    { title: "Title", field: "title", align: "left", width: 500 },
    { title: "Intro", field: "intro", align: "left", width: 500 },
    { title: "Ngày tạo", field: "startDate", align: "left", width: 200 },
    { title: "Ngày update", field: "updateDate", align: "left", width: 200 },
    { title: "Active", field: "active", align: "left", width: 100 },
    {
      width: 100, formatter: "buttonCross", align: "center", title: "Xóa tin", headerSort: false, cellClick: (e, cell) => {
        this.utilsService.presentAlertConfirmDelete(cell.getRow()._row.data.id, "Thông báo xóa tin tức", ' <strong>Tin tức sẽ bị xóa</strong>', "News");
        event.stopPropagation();
      }
    }
  ];
  listNews: any[] = [];
  constructor(private constantsService: ConstantsService,
    public db: AngularFireDatabase,
    private alertCtrl: AlertController,
    public utilsService: UtilsService) {
    this.getListNews();
  }

  getListNews() {
    let listN = this.db.list("News/");
    listN.valueChanges().subscribe(event => {
      this.listNews = [];
      if (event) {
        for (let index = 0; index < event.length; index++) {
          const element: any = event[index];
          this.listNews.push(element);
        }
      }
    });
  }
}
