import { Component, OnInit, ViewChild } from '@angular/core';
import { PopoverController } from '@ionic/angular';
import { UtilsService } from 'src/app/services/utils.service';
import { AppValueService } from 'src/app/services/app-value.service';
import { TranslateService } from '@ngx-translate/core';
import { NewsManagementService } from './news-management.service';
import { ModalEditNewsComponent } from 'src/app/components/modal-edit-news/modal-edit-news.component';
@Component({
  selector: 'app-news-management',
  templateUrl: './news-management.page.html',
  styleUrls: ['./news-management.page.scss'],
})
export class NewsManagementPage implements OnInit {
  @ViewChild('contentSearch') contentSearch: any;  
  constructor(private popoverController: PopoverController,
    public newsService: NewsManagementService,
    public utilsService: UtilsService,
    public appValueService: AppValueService,
    private translate: TranslateService) { }

  ngOnInit() {
  }
  showDetail(data: any, mode: any) {
    if(mode == "edit"){
      this.showModalEditNews(data.ev, data.row._row.data);
    }
    else {
      this.showModalEditNews(null, null);
    }    
  }

  async showModalEditNews(ev: any, row: any) {
    const popover = await this.popoverController.create({
      component: ModalEditNewsComponent,
      id: "editNewsModal",
      event: ev,
      translucent: true,
      componentProps: { rowProduct: row },
      cssClass: "edit-news-popover"
    });
    return await popover.present();
  }
  ionViewDidEnter() {
    let removeHeight = [];
    this.appValueService.APP_TABULATOR_HEIGHT = [this.contentSearch, removeHeight];
    this.newsService.heightTable = this.utilsService.settingTableHeight(this.appValueService.APP_TABULATOR_HEIGHT);
  }
}
